@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#000}header,main,footer{font-family:'Roboto', sans-serif}#home{padding-top:1rem}.carousel .carousel-item{height:600px}.carousel-item img{position:absolute;object-fit:cover;top:0;left:0;min-height:600px}header{background:#a5d6a7;font-weight:300}header .navbar-brand{font-size:1.5rem}.navbar-brand img{width:1.5rem;height:1.5rem;margin:0 1rem 0 1rem}main{background:#e8f5e9}#contact{padding-bottom:1rem}.contact-form{background:#fff;margin:auto}.contact-image{text-align:center}.contact-image img{border-radius:6rem;width:11%;margin-top:-3%;-webkit-transform:rotate(29deg);transform:rotate(29deg)}.contact-form form{padding:5% 14% 10% 14%}.contact-form h3{margin-bottom:8%;text-align:center;color:#18a597}.contact-form .btnContact{width:50%;border:none;border-radius:1rem;padding:1.5%;background:#dc3545;font-weight:600;color:#fff;cursor:pointer;margin-top:5%}footer{background-color:#000;color:#fff}footer p{text-align:center}

